<template>
  <div>
    <div>
      <HeaderOpen v-show="showView" />
    </div>

    <header v-if="inform">
      <div class="topCont">
        <div class="img">
          <img
            :src="`https://imagev2.xmcdn.com/${inform?.anchorInfo?.userInfo?.logo}!op_type=3&columns=144&rows=144&magick=webp`"
            alt=""
          />
        </div>
        <div class="cont">
          <div class="title">{{ inform.anchorInfo.userInfo.title }}</div>
          <div class="intro">
            简介:{{
              inform.anchorInfo.userInfo.personalSignature
                ? inform.anchorInfo.userInfo.personalSignature
                : "暂无"
            }}
          </div>
          <div class="authentication" v-if="inform.anchorInfo.ptitle">
            喜马认证:{{ inform.anchorInfo.userInfo.ptitle }}
          </div>
        </div>
      </div>
      <div class="outline">
        <div class="left">
          <span>关注</span>
          {{
            inform.anchorInfo.statCountInfo.followingCount > 100000000
              ? (inform.anchorInfo.statCountInfo.followingCount / 100000000).toFixed(2) +
                "亿"
              : inform.anchorInfo.statCountInfo.followingCount > 10000
              ? (inform.anchorInfo.statCountInfo.followingCount / 10000).toFixed(2) + "万"
              : inform.anchorInfo.statCountInfo.followingCount
          }}
        </div>
        <div class="right">
          <span>粉丝</span>
          {{
            inform.anchorInfo.statCountInfo.followerCount > 100000000
              ? (inform.anchorInfo.statCountInfo.followerCount / 100000000).toFixed(2) +
                "亿"
              : inform.anchorInfo.statCountInfo.followerCount > 10000
              ? (inform.anchorInfo.statCountInfo.followerCount / 10000).toFixed(2) + "万"
              : inform.anchorInfo.statCountInfo.followerCount
          }}
        </div>
      </div>
      <div class="bottom">+ 关注</div>
    </header>

    <!-- 滑动切换  4个组件-->
    <van-tabs swipeable>
      <van-tab v-for="index in contentName" :key="index" :title="index">
        <div class="content" v-show="index === '主页'">
          <InformationPage :inform="inform" />
        </div>
        <div class="content" v-show="index === '专辑'"><AlbumPage /></div>
        <div class="content" v-show="index === '声音'"><VoicePage /></div>
        <div class="content" v-show="index === '订阅'"><SubscribePage /></div>
      </van-tab>
    </van-tabs>
    <div>
      <Footer v-show="showView" />
    </div>
  </div>
</template>

<script>
import InformationPage from "@/components/informationPage.vue";
import AlbumPage from "@/components/AlbumPage.vue";
import VoicePage from "@/components/voicePage.vue";
import SubscribePage from "@/components/subscribePage.vue";
import HeaderOpen from "@/components/headerOpen.vue";
import Footer from "@/components/footerContent.vue";

export default {
  components: {
    InformationPage,
    AlbumPage,
    VoicePage,
    SubscribePage,
    Footer,
    HeaderOpen,
  },
  data() {
    return {
      contentName: ["主页", "专辑", "声音", "订阅"],
      //内容更新完成后头部与尾部显示
      showView: false,
      //主播信息
      inform: null,
      //专辑信息
      album: null,
      //声音信息
      voice: null,
      //订阅信息
      subscr: null,
    };
  },
  created() {
    this.showView = false;
    //拿到作者详情
    let first = this.axios.get(
      `https://apis.netstart.cn/ximalaya/anchor/queryAnchorPage/${this.$route.params.id}`,
      {
        params: {
          pageSize: 20,
          tabType: 2,
        },
      }
    );

    Promise.all([first]).then((res) => {
      this.showView = true;
      // console.log(res[0].data.data);
      this.inform = res[0].data.data;
    });
  },
};
</script>

<style lang="less" scoped>
header {
  .topCont {
    display: flex;
    align-items: center;
    .img {
      width: 85rem;
      height: 85rem;
      flex-shrink: 0;
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
    }
    .cont {
      display: flex;
      flex-direction: column;
      justify-content: stretch;
      .title {
      }
      .intro {
      }
      .authentication {
      }
    }
  }
  .outline {
    .left {
      span {
      }
    }
    .right {
      span {
      }
    }
  }
  .bottom {
  }
}
</style>
